:host {
  display: block;
}
.booking-info {
  border-bottom: 1px solid var(--gray-300, hsl(217, 16%, 84%));
}
.booking-info-text,
.room-info-text {
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--gray-800, hsl(214, 33%, 17%));
}
.booking-info-text span,
.room-info-text span {
  font-weight: 400;
  color: var(--gray-600, hsl(218, 24%, 27%));
}
section,
.room-info {
  padding: 1rem 0;
}
.booking-details-header {
  font-size: 1.1rem;
  font-weight: 600;
}
.room-type {
  margin-bottom: 0.3rem;
  font-weight: 600;
  font-size: 1rem;
}
p {
  font-size: 0.875rem;
}

.total-payment {
  font-size: 1rem;
}
